import React, { FC } from "react"
import * as classes from './EventItem.module.scss'
import { Button, Col, Container, Row, } from "reactstrap"
import { ReportContainer } from "@/entities/EventReport"
import { EventItemResultType } from "../model/EventItemSlice"
import { Presentation } from "@/entities/EventPresentation"
import { Plan } from "@/entities/EventPlan"
import { Comment } from "@/entities/EventComment"
import 'bootstrap/dist/css/bootstrap.min.css';
import { useAppDispatch } from "@/app/lib/hooks/redux"
import { send } from "@/processes/event"

interface EventItemProps {
    menuType: EventItemResultType,
    withPlan: boolean,
    // setCurentEventProp: (
    //     name: EVENT_PROPS,
    //     value: BXUser | null
    // ) => void

    //todo
    // send
    //cancelResultMenu
}




const EventItem: FC<EventItemProps> = ({
    menuType,
    withPlan,
    // setCurentEventProp,
    // send,
    // cancelResultMenu
}) => {
    const dispatch = useAppDispatch();
    const sendEvent = () => {
        dispatch(
            send()
        )
    }

    return <React.Fragment>

        <Container fluid={true} className={classes.wrapper}>

            <Row className={`${classes.topArea} p-0`}>
                <Col sm={5} className="p-0">
                    <Row>
                    <Col sm={12} className="p-0">
                        <ReportContainer
                            isNoResult={false}
                        />
                    </Col>
                    {/* <Col className={classes.line} sm={1}></Col> */}
                    <Col sm={12} className="mt-2 p-0">
                        <Comment />
                    </Col>
                    </Row>
                </Col>
                <Col sm={1} className={classes.line}></Col>
                <Col sm={4} className="p-0">
                    {/* <Row> */}
                    <Col sm={12} className="p-0 m-0">
                        {menuType === 'result'
                            ? <Presentation />
                            : <ReportContainer isNoResult={true} />
                        }
                    </Col>

                    <Col sm={12} className="mt-2 p-0">
                        <Plan />
                    </Col>
                    {/* </Row> */}
                </Col>
                <Col sm={2} className={`${classes.actionsWrapper} p-0`}>
                    <div className={classes.actions}>
                        <Button  className={classes.btn_send} size='sm'
                            onClick={sendEvent}
                        >
                            Отправить
                        </Button>
                        <Button   className={`${classes.btn_cancel} mt-2`} size='sm'
                        // onClick={cancelResultMenu}
                        >
                            Отмена
                        </Button>
                    </div>

                </Col>
            </Row>
            {/* <Row className={`${classes.bottomArea}`}>
                <Col sm={5} className="p-0">
                    {menuType === 'result'
                        ? <Presentation />
                        : <ReportContainer isNoResult={true} />
                    }




                </Col >
                <Col className={classes.line} sm={1}></Col>
                {withPlan &&
                    <Col sm={5} className="p-0">
                        <Plan />
                    </Col>
                }
            </Row> */}
        </Container>



        {/* </div> */}
    </React.Fragment >
}

export default EventItem