import React, { FC, ReactNode } from 'react';
import { Container } from 'reactstrap';
import * as classes from './Page.module.scss'

interface PageProps {
    color: 'white' | 'grey'
    children?: ReactNode;  // Тип ReactNode охватывает все возможные типы, которые могут быть переданы как children
}


const Page: FC<PageProps> = ({ color, children }) => {

    let pageClass = classes.pageWhite

    if (color === 'grey') {
        pageClass = classes.pageGrey
    }


    return (
        <React.Fragment>
            <div className={`${pageClass} ${classes.page} page-content p-1`}>
                {/* <Container fluid={true} className={`${pageClass} ${classes.page} p-0`}> */}
                    {/*  <Row className="p-0">
                        <Col xl={12} className="p-0
                        d-flex 
                        flex-row 
                        justify-content-centr 
                        align-items-centr
                        "> */}
                    {children}
                    {/* </Col>
                    </Row> */}
                {/* </Container> */}
            </div>
        </React.Fragment>
    );
}

export default Page;