import Plan from "./Plan"
import { useAppDispatch, useAppSelector } from "@/app/lib/hooks/redux"
import { EV_PLAN_PROP } from "../type/event-plan-type"
// import { EVENT_PROP } from "@/processes/event/types/event-types"
import { EV_PLAN_STATE_ITEM, eventPlanActions } from "../model/EventPlanSlice"
// import { eventActions } from "@/processes/event"
// import { BXUser } from "@/app/types/bitrix/bitrix-type"
import { FC } from "react"
// import { DEPARTAMENT_STATE_PROP, DUSER_ROLE } from "@/features/Departament/type/department-type"


export const PlanContainer: FC = () => {

    // const eventState = useAppSelector(state => state.event)
    const planState = useAppSelector(state => state.eventPlan)
    // const departmentState = useAppSelector(state => state.department)
    // const departmentPlan = departmentState[DEPARTAMENT_STATE_PROP.PLAN]
    // const responsibile = departmentPlan[DUSER_ROLE.RESPONSIBLE]
    // const createdBy = departmentPlan[DUSER_ROLE.CREATED_BY]

    const type = planState[EV_PLAN_PROP.TYPE] as EV_PLAN_STATE_ITEM
    const date = planState[EV_PLAN_PROP.DATE]
    // const name = planState[EV_PLAN_PROP.NAME]


    const dispatch = useAppDispatch();

    // const eventPropChange = (type: EVENT_PROP, value: BXUser): void => {

    //     dispatch(
    //         eventActions
    //             .setPrimrtiveStateProp(
    //                 {
    //                     name: type,
    //                     value: value
    //                 }
    //             )
    //     )



    // }
    const planPropChange = (type: EV_PLAN_PROP, value: string): void => {

        dispatch(
            eventPlanActions
                .setPlanProp(
                    {
                        //@ts-ignore
                        name: type,
                        //@ts-ignore
                        value: value
                    }
                )
        )


    }

    return <Plan
        // responsibile={responsibile}
        // createdBy={createdBy}
        type={type}
        date={date}
        // name={name}
        // eventPropChange={eventPropChange}
        planPropChange={planPropChange}

    />
}



